.about__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;

  @include vp-1023 {
    gap: 30px;
  }

  @include vp-767 {
    display: block;
  }
}

.about__text {
  align-self: center;
}

.about h2 {
  text-align: start;
  margin-top: 0;
  margin-bottom: 30px;

  @include vp-1023 {
    margin-bottom: 16px;
  }

  @include vp-767 {
    margin-top: -8px;
    margin-bottom: 15px;
  }
}

.about p {
  margin: 0;

  @include vp-1023 {
    margin-bottom: 5px;
  }

  @include vp-767 {
    margin-bottom: 33px;
  }
}

.about img {
  width: 100%;
  height: 301px;
  object-fit: cover;

  @include vp-1023 {
    height: 100%;
  }

  @include vp-767 {
    min-height: 186px;
  }
}
